import { h } from "preact";
import StepActions from "../../step-actions";
import { TOKENS } from "../../../constants";
import { formatNumber } from "../../../utils";

const HyphaTokenSalesRoundInfo = ({ usdPerHypha, roundNo, totalRounds }) => (
  <div id="token-sale-info">
    <p class="welcom">Welcome to the</p>
    <p class="hts">
      Hypha Token Sale{" "}
      <span class="htsn">
        <span id="sale-round">{roundNo}</span> /{totalRounds}
      </span>
    </p>

    <p class="hypha-price">
      Hypha token price for this round is:{" "}
      <span id="hypha-value-usd">{usdPerHypha}</span>$
    </p>
    <p class="p-small">
      Each round is 100,000 tokens, with $1.00 per token in the first round.{" "}
      <br />
      Price will increase by $0.03 each round.
    </p>
  </div>
);

const DaoActivationAmount = ({ usdPerHypha, amount }) => {
  const hyphaPretty = formatNumber(amount);
  const usdPretty = formatNumber(usdPerHypha * amount);
  return (
    <div id="amount-info">
      <p>Hypha Tokens to buy:</p>
      <p class="hypha-amount-purchase">
        <img class="hypha-curr" src="img/round-logo.svg" />
        <span id="hypha-amount-to-buy">{hyphaPretty}</span>
      </p>
      <p class="conversion">
        <span id="usd-amount-to-buy">{usdPretty}</span>
        <span class="doll">
          <strong>USD</strong>
        </span>
      </p>
    </div>
  );
};

const Welcome = ({
  account,
  isDaoActivation,
  roundNo,
  totalRounds,
  token,
  amount,
  usdPerHypha,
  setToken,
  next,
  previous,
}) => {
  return (
    <div id="step-2">
      {!isDaoActivation && (
        <div class="step-nav">
          <div class="stp done"></div>
          <div class="stp active">
            <p>2</p>
          </div>
          <div class="stp"></div>
          <div class="stp"></div>
        </div>
      )}

      <div class="steps-top">
        <div class="account-welcome">
          <div id="account-img">
            <img src={account.profilePhoto} />
          </div>
          <p>
            hi{" "}
            <span id="account-name">{account.nickname || account.account}</span>{" "}
          </p>
        </div>
        {isDaoActivation ? (
          <DaoActivationAmount amount={amount} usdPerHypha={usdPerHypha} />
        ) : (
          <HyphaTokenSalesRoundInfo
            usdPerHypha={usdPerHypha}
            roundNo={roundNo + 1}
            totalRounds={totalRounds}
          />
        )}
      </div>

      <div class="steps-bottom">
        <h4>Payment Method</h4>
        <p>Select how you would like to buy Hypha token</p>

        <div id="payment-options">
          <div
            class={
              token === TOKENS.EOS ? "token-select selected" : "token-select"
            }
            id="choose-EOS"
            onClick={() => setToken(TOKENS.EOS)}
          >
            <div class="token-pay">
              <img src="/img/buy-hypha-tokens/eos.png" />
            </div>
            <p>EOS</p>
          </div>

          <div
            class={
              token === TOKENS.BTC ? "token-select selected" : "token-select"
            }
            id="choose-BTC"
            onClick={() => setToken(TOKENS.BTC)}
          >
            <div class="token-pay">
              <img src="/img/buy-hypha-tokens/btc.png" />
            </div>
            <p>BTC</p>
          </div>
        </div>

        <StepActions next={next} previous={previous} />
      </div>
    </div>
  );
};

export default Welcome;
