import { h } from "preact";
import { useState } from "preact/hooks";
import StepActions from "../../step-actions";

import { getErrorMessage } from "../../../utils";
import { ACCOUNT_TYPE } from "../../../constants";

const Account = ({
  next,
  previous,
  accountName: defaultAccountName,
  accountType,
  setAccount,
  setAccountType,
  createAccountLink,
  createAccountLinkText,
  widgetText2,
  widgetText1,
  getAccountDetails,
}) => {
  const [loading, setLoading] = useState();
  const [error, setError] = useState();
  const [accountName, setAccountName] = useState(defaultAccountName);

  const handleInputChange = (event, props) => {
    const accountName = event.target.value.toLowerCase();
    setError(false);
    setAccountName(accountName);
  };

  const getAccountAndNavigate = async () => {
    setLoading(true);
    try {
      await getAccountDetails(accountName);
      await next();
    } catch (e) {
      setLoading(false);
      setError("account-not-found");
    }
  };

  const toggleAccountType = () => {
    setAccountType(
      accountType === ACCOUNT_TYPE.SEEDS
        ? ACCOUNT_TYPE.HYPHA_TELOS
        : ACCOUNT_TYPE.SEEDS
    );
  };

  return (
    <div id="step-1">
      <div class="step-nav">
        <div class="stp active">
          <p>1</p>
        </div>
        <div class="stp"></div>
        <div class="stp"></div>
        <div class="stp"></div>
      </div>

      <div class="steps-top">
        <h2>Account Name</h2>
        <p>{widgetText1}</p>
        <p className="p-small">
          {widgetText2}
          <a href={createAccountLink}>{createAccountLinkText}</a>
        </p>
        <p>
          <br />
          <b>
            <button class="switch-account-btn" onClick={toggleAccountType}>
              Switch to {accountType === ACCOUNT_TYPE.SEEDS ? "Hypha" : "SEEDS"}{" "}
              Account
            </button>
          </b>
        </p>
      </div>
      <div class="steps-bottom">
        <input
          id="account-name"
          value={accountName}
          placeholder={`Enter your ${
            accountType === ACCOUNT_TYPE.SEEDS ? "SEEDS" : "Hypha"
          } account`}
          class={error && "error"}
          onChange={handleInputChange}
        />
        {error && <div class="error-message">{getErrorMessage(error)}</div>}

        <StepActions
          next={next && getAccountAndNavigate}
          previous={previous}
          loading={loading}
        />
      </div>
    </div>
  );
};

export default Account;
